const initState = {
    listProduct : [],
    sumPrice : 0,
    moneyCoupon:0
}

const cartReducer = (state= initState,action)=>{
    switch(action.type){
        case 'ADD_CART':{
            let newListCart = [...state.listProduct];

            let x = newListCart.find(item => item.id === action.payload.id);
            // thêm sản phẩm khi chưa có trong giỏ hàng
            if(x===undefined){
                newListCart.push(action.payload);
            }
            // cập nhật số lượng khi sản phẩm đã có trong giỏ hàng
            else{
                newListCart = newListCart.map((item,index)=>{
                    if(item.id===action.payload.id){
                        item.quantity++;
                        item.total = item.quantity*item.price;
                        return item;
                    }
                    else{
                        return item;
                    }
                });
            }
            // Tính tổng giá tiền của giỏ hàng
            let sum = 0;
            for(let i = 0;i<newListCart.length;i++){
                sum = sum + newListCart[i].total;
            }

            return {
                ...state,
                listProduct:newListCart,
                sumPrice: sum
            };
        }
        case 'DELETE_CART':{
            let newListCart = [...state.listProduct];
            for(let i = 0; i< newListCart.length;i++){
                if(newListCart[i].id === action.payload){
                    newListCart.splice(i,1);
                }
            }

            let sum = 0;
            for(let i = 0;i<newListCart.length;i++){
                sum = sum + newListCart[i].total;
            }
            return {
                ...state,
                listProduct:newListCart,
                sumPrice: sum
            };
        }
        case "ADD_COUPON":{
            return {
                ...state,
                moneyCoupon:action.payload
            }
        }

        default:
            return state;
    }
}

export default cartReducer;