import React, { useState } from "react";
import { useSelector } from "react-redux";

import { Link, Redirect } from "react-router-dom";

import Banner from "../../../../components/banner/banner";
import TableProductCart from "../../components/tableProductCart/tableProductCart";

import "./style.scss";

function ListCart(props) {
  const sumPrice = useSelector((state) => state.cart.sumPrice);
  const listCart = useSelector((state) => state.cart.listProduct);
  const username = useSelector((state) => state.user.user); //get from redux
  const [user, setUser] = useState(username);

  // const handlePayment = (listCart) => {
  //   if (user !== "") {
  //     axios
  //       .get("http://localhost:9080/khachhang/getIdCustomer/" + user)
  //       .then((res) => {
  //         if (res.data.id !== null) {
  //           axios
  //             .post("http://localhost:9080/order/addorder", {
  //               idKhachHang: res.data.id,
  //               sumPrice: sumPrice,
  //               orderDate: Date.now(),
  //               listProduct: listCart,
  //             })
  //             .then((res) => {
  //               alert(res.data.order);
  //             });
  //         } else {
  //           alert("User khong ton tai");
  //         }
  //       })
  //       .catch((err) => alert(err));
  //   }
  //   if (user === "") {
  //     setUser("notLogin");
  //   }
  // };

  const handleCheckoutProduct = () => {
    if (user === "") {
      setUser("notLogin");
    }
  };
  // xử lý sự kiện click button checkout
  let button;
  if (user !== "" && user !== "notLogin" && listCart.length > 0) {
    button = (
      <Link to="/checkoutorder">
        <button className="btn-thanhtoan">Proceed to checkout</button>
      </Link>
    );
  } else {
    button = (
      <button className="btn-thanhtoan" onClick={() => handleCheckoutProduct()}>
        Proceed to checkout
      </button>
    );
  }

  return (
    <div>
      <Banner title="Page cart" shoeImg={true} />
      <div className="container">
        <h3 className="mt-4 mb-4">List product cart</h3>
        <TableProductCart />
        <div className="payment">
          <div>
            <p className="sum-price">Sum price: {sumPrice}</p>
            {button}
          </div>
        </div>
        {listCart.length > 0 ? (
          ""
        ) : (
          <div className="alert alert-danger mt-3" role="alert">
            Cart empty
          </div>
        )}
      </div>
      {user === "notLogin" ? <Redirect to="/login" /> : ""}
    </div>
  );
}
export default ListCart;
