export const addToCart = (product)=>{
    return {
        type:"ADD_CART",
        payload:product
    }
}
//chua xu ly
export const changeQuantity = (quantity)=>{
    return{
        type:"CHANGE_QUANTITY",
        payload: quantity
    }
}

export const deleteCart = (id)=>{
    return {
        type:"DELETE_CART",
        payload: id
    }
}

export const addCoupon = (moneyCoupon)=>{
    return{
        type:"ADD_COUPON",
        payload: moneyCoupon
    }
}