import React, { useEffect, useState } from "react";
import axios from "axios";

import Banner from "../../../../components/banner/banner";
import ItemProduct from "../../components/itemProduct/itemProduct";

import "./style.scss";
function Home(props) {
  const [productBestSeller, setProductBestSeller] = useState([]);
  useEffect(() => {
    axios
      .get("http://localhost:9080/sanpham/bestseller")
      .then((res) => {
        setProductBestSeller(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <div>
      <Banner title="Home Page" shoeImg={true} />

      <div className="container mt-4">
        <h3>Product best seller</h3>
        <div className="list-product-bestseller d-flex mb-5">
          {productBestSeller.map((item, index) => (
            <ItemProduct
              name={item.name}
              price={item.price}
              image={item.image}
              id={item._id}
              key={index}
            />
          ))}
        </div>
      </div>
    </div>
  );
}


export default Home;
