import React, { useState } from "react";
import axios from "axios";

import "./style.scss";
import { Redirect } from "react-router-dom";

function FormRegister(props) {
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [username, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const [address, setAddress] = useState("");
  const [birthday, setBirthday] = useState("");
  const [flagRegister, setFlagRegister] = useState("");
  const handleRegisterUser = (
    name,
    phone,
    birthday,
    address,
    username,
    password
  ) => {
    axios
      .post("http://localhost:9080/user/register", {
        username: username,
        password: password,
        name: name,
        phone: phone,
        birthday: birthday,
        address: address,
      })
      .then((res) => {
        if (res.data.create === "success") {
          setFlagRegister(<Redirect to="/login" />);
        } else {
          setFlagRegister(
            <div class="alert alert-danger" role="alert">
              Register fail!
            </div>
          );
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <div className="form-register">
      <div className="content-register">
        <h3 className="text-center text-uppercase">Register</h3>
        <div className="row mt-4">
          <div className="col-md-6">
            <div className="mt-3">
              <label htmlFor="name">Name</label>
              <input
                type="text"
                placeholder="Add name"
                className="input-register"
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="mt-3">
              <label htmlFor="phone">Phone</label>
              <input
                type="number"
                placeholder="Add phone"
                className="input-register"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className="mt-3">
              <label htmlFor="username">Username</label>
              <input
                type="text"
                name="username"
                placeholder="Username"
                className="input-register"
                autoComplete="off"
                value={username}
                onChange={(e) => setUserName(e.target.value)}
              />
            </div>
            <div className="mt-3">
              <label htmlFor="username">Password</label>
              <input
                type="password"
                name="password"
                placeholder="Password"
                className="input-register mb-3"
                autoComplete="off"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
          </div>
          <div className="col-md-6 pl-4">
            <div className="mt-3">
              <label htmlFor="birthday">Birthday</label>
              <input
                type="date"
                className="input-register"
                value={birthday}
                onChange={(e) => setBirthday(e.target.value)}
              />
            </div>
            <div className="mt-3">
              <label htmlFor="address">Address</label>
              <input
                type="text"
                className="input-register"
                placeholder="Add address"
                value={address}
                onChange={(e) => setAddress(e.target.value)}
              />
            </div>
          </div>
        </div>
        <div className="w-100">{flagRegister}</div>

        <button
          className="btn-register"
          onClick={() =>
            handleRegisterUser(
              name,
              phone,
              birthday,
              address,
              username,
              password
            )
          }
        >
          Register
        </button>
      </div>
    </div>
  );
}

export default FormRegister;
