import React, { useState } from "react";
import { useSelector } from "react-redux";
import axios from "axios";
import { Redirect } from "react-router-dom";

import InputFormCheckout from "../inputFormCheckout/inputFormCheckout";
import "./style.scss";
import ButtonEffect from "../../../../components/button/buttonEffect/buttonEffect";


function FormCheckout(props) {
  const [nameReceive, setNameReceive] = useState("");
  const [phoneReceive, setPhoneReceive] = useState("");
  const [addressReceive, setAddressReceive] = useState("");
  const listCart = useSelector((state) => state.cart.listProduct);
  const sumPrice = useSelector((state) => state.cart.sumPrice);
  const moneyCoupon = useSelector((state)=>state.cart.moneyCoupon);
  const user = useSelector((state) => state.user.user);
  const [flagPaypal,setFlagPaypal] = useState(false);
  const [idNewHoaDon,setIdNewHoaDon] = useState("");
  let path = "/orderconfirm/"+idNewHoaDon;
  const handlePayment = (listCart) => {
    axios
      .get("http://localhost:9080/khachhang/getIdCustomer/" + user)
      .then((res) => {
        if (res.data.id !== null) {
          axios
            .post("http://localhost:9080/order/addorder", {
              idKhachHang: res.data.id,
              sumPrice: sumPrice,
              orderDate: Date.now(),
              listProduct: listCart,
              nameReceive: nameReceive,
              phoneReceive: phoneReceive,
              addressReceive: addressReceive,
              moneyCoupon: moneyCoupon,
            })
            .then((res) => {
              if(res.data.order === "success"){
                axios.get("http://localhost:9080/order/detailorder/"+res.data.idNewHoaDon).then(res=>{
                  setIdNewHoaDon(res.data.hoadon[0]._id);
                  setFlagPaypal(true);
                }).catch(err=>{
                  console.log(err);
                })
                
              }
            });
        } else {
          alert("User khong ton tai");
        }
      })
      .catch((err) => alert(err));
  };
  return (
    <div className="mr-3">
      <div className="mt-3">
        <InputFormCheckout
          value={nameReceive}
          placeholder="Enter Recipient's name"
          onChange={(e) => setNameReceive(e.target.value)}
          name="nameReceive"
          type="text"
        />
      </div>
      <div className="mt-3">
        <InputFormCheckout
          value={phoneReceive}
          placeholder="Enter Recipient's phone"
          onChange={(e) => setPhoneReceive(e.target.value)}
          name="phoneReveice"
          type="number"
        />
      </div>
      <div className="mt-3">
        <InputFormCheckout
          value={addressReceive}
          placeholder="Enter Recipient's address"
          onChange={(e) => setAddressReceive(e.target.value)}
          name="addressReceive"
          type="text"
        />
      </div>
      <div className="d-flex justify-content-end">
        <div className="w-25 mt-3 mr-4">
          <ButtonEffect
            title="PROCEED TO PAYPAL"
            onClick={() => handlePayment(listCart)}
          />
        </div>
      </div>
      {
        
          flagPaypal === true?<Redirect to={path}/>:""
      }
    </div>
  );
}

export default FormCheckout;
