import React from "react";
import { useSelector } from "react-redux";

import "./style.scss";

function DetailProductCart(props) {
  const listCart = useSelector((state) => state.cart.listProduct);
  const sumPrice = useSelector((state) => state.cart.sumPrice);
  const moneyCoupon = useSelector((state) => state.cart.moneyCoupon);
  return (
    <div>
      <table className="w-100 text-center table-detail-product-cart">
        <thead>
          <tr>
            <th className="text-left">Products</th>
            <th></th>
            <th className="text-right">Total</th>
          </tr>
        </thead>
        <tbody>
          {listCart.map((item, index) => {
            return (
              <tr key={index}>
                <td className="text-left">{item.name}</td>
                <td>x{item.quantity}</td>
                <td className="text-right">{item.total}</td>
              </tr>
            );
          })}
          <tr>
            <th className="text-left">SUBTOTAL</th>
            <th></th>
            <th className="text-right">{sumPrice}</th>
          </tr>
          {moneyCoupon !== 0 ? (
            <tr>
              <th className="text-left">COUPON</th>
              <th></th>
              <th className="text-right">{moneyCoupon}</th>
            </tr>
          ) : (
            ""
          )}
          <tr>
            <th className="text-left">TOTAL</th>
            <th></th>
            <th className="text-right">{sumPrice - moneyCoupon}</th>
          </tr>
        </tbody>
      </table>
    </div>
  );
}

export default DetailProductCart;
