import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import moment from "moment";

import Banner from "../../../../components/banner/banner";

import "./style.scss";

function OrderConfirm(props) {
  let { id } = useParams();
  const [order, setOrder] = useState({ product: [] });
  const [khachhang, setkhachhang] = useState({});
  useEffect(() => {
    axios
      .get("http://localhost:9080/order/detailorder/" + id)
      .then((res) => {
        setOrder(res.data.hoadon[0]);
        axios
          .get(
            "http://localhost:9080/khachhang/getkhachhang/" +
              res.data.hoadon[0].idKhachHang
          )
          .then((res) => {
            if (res.data.khachhang !== null) {
              setkhachhang(res.data.khachhang[0]);
            }
          })
          .catch((err) => {
            console.log("loi getKhachhang:", err);
          });
      })
      .catch((err) => {
        console.log("loi detailorder:", err);
      });
  }, []);
  return (
    <div>
      <Banner title="Confirmation page" shoeImg={true} />
      <div className="container mt-4">
        <p className="text-center notification p-4">
          Thank you. Your order has been received.
        </p>
        <div className="info-order">
          <div className="row">
            <div className="col-md-4">
              <p>Order Info</p>
              <table className="w-75">
                <thead></thead>
                <tbody>
                  <tr>
                    <td>Order date:</td>
                    <td>{moment(order.orderDate).format("DD-MM-YYYY")}</td>
                  </tr>
                  <tr>
                    <td>Money coupon:</td>
                    <td>{order.moneyCoupon}</td>
                  </tr>
                  <tr>
                    <td>Total:</td>
                    <td>{order.sumPrice}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div className="col-md-4">
              <p>Customer address</p>
              <table className="w-75">
                <thead></thead>
                <tbody>
                  <tr>
                    <td>Name:</td>
                    <td>{khachhang.name}</td>
                  </tr>
                  <tr>
                    <td>Phone:</td>
                    <td>{khachhang.phone}</td>
                  </tr>
                  <tr>
                    <td>Address:</td>
                    <td>{khachhang.address}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div className="col-md-4">
              <p>Shipping address</p>
              <table className="w-75">
                <thead></thead>
                <tbody>
                  <tr>
                    <td>Name:</td>
                    <td>{order.nameReceive}</td>
                  </tr>
                  <tr>
                    <td>Phone:</td>
                    <td>{order.phoneReceive}</td>
                  </tr>
                  <tr>
                    <td>Address:</td>
                    <td>{order.addressReceive}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div className="order-details p-2 mt-5">
          <p className="mt-3 font-weight-bold">Order details</p>
          <div className="list-product pl-4 pr-4">
            <table className="w-100">
              <thead>
              <tr>
                  <th>Product</th>
                  <th>Quantity</th>
                  <th>Total</th>
                </tr>
              </thead>
              <tbody>
                
                {order.product.map((item, index) => {
                  return (
                    <tr key={index}>
                      <td>{item.name}</td>
                      <td>x{item.quantity}</td>
                      <td>{item.total}</td>
                    </tr>
                  );
                })}
                <tr className="font-weight-boder">
                  <td>SUBTOTAL</td>
                  <td></td>
                  <td>{order.sumPrice}</td>
                </tr>
                <tr className="font-weight-bold">
                  <td>MONEY COUPON</td>
                  <td></td>
                  <td>{order.moneyCoupon}</td>
                </tr>
                <tr className="font-weight-bold">
                  <td>TOTAL</td>
                  <td></td>
                  <td>{order.sumPrice - order.moneyCoupon}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
}

export default OrderConfirm;
