import React, { useEffect, useState } from "react";
import axios from "axios";
import { useSelector } from "react-redux";

import Banner from "../../../../components/banner/banner";
import ListOrder from "../../components/listOrder/listOrder";

import "./style.scss";

function OrderCustomer(props) {
  const user = useSelector((state) => state.user.user);
  const [listHoaDon, setListHoaDon] = useState([]);
  useEffect(() => {
    axios
      .get("http://localhost:9080/khachhang/getIdCustomer/" + user)
      .then((res) => {
        if (res.data.id !== null) {
          axios
            .get("http://localhost:9080/order/getlistorder/" + res.data.id)
            .then((res) => {
              setListHoaDon(res.data.hoadon);
            })
            .catch((err) => {
              console.log("loi getlistorder:", err);
            });
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, [listHoaDon]);
  return (
    <div>
      <Banner title="List Order Page" shoeImg={true} />
      <div className="container mt-4">
        <h3>List order</h3>
        <ListOrder list={listHoaDon} />
      </div>
    </div>
  );
}

export default OrderCustomer;
