import React from 'react'

//import static file
import "./style.scss";

//import image
import shoe from "../../assets/banner/banner-img.png"

function Banner(props) {
    return (
        <div className="banner">
            {
                props.shoeImg === true?<img className="img-shoe" src={shoe} alt="shoe"/>:""
            }
            <p className="title">{props.title}</p>
        </div>
    )
}


export default Banner

