import React from "react";
import Banner from "../../../../components/banner/banner";
import DetailProductCart from "../../components/detailProductCart/detailProductCart";
import FormCheckout from "../../components/formCheckout/formCheckout";
import FormCoupon from "../../components/formCoupon/formCoupon";

import "./style.scss";

function CheckoutOrder(props) {
  return (
    <div>
      <Banner title="Checkout page" shoeImg={true} />
      <div className="container mt-4">
        <h3>Checkout</h3>
        <div>
          <p>Coupon code</p>
          <FormCoupon />
        </div>
        <div className="detail-checkout mt-4">
          <span>Billing details</span>
          <div className="row">
            <div className="col-md-8">
              <FormCheckout />
            </div>
            <div className="col-md-4">
              <div className="detail-product-cart p-3">
                <span className="title">Your order</span>
                <br />
                <hr />
                <DetailProductCart />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CheckoutOrder;
