import React from 'react';
import './App.scss';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";

import Home from './features/home/pages/home/home';
import Header from './components/header/header';
import DetailProduct from './features/product/pages/detailProduct/detailProduct';
import ListCart from './features/cart/pages/listCart/listCart';
import Login from './features/login/pages/login/login';
import Register from './features/login/pages/register/register';
import InfoCustomer from './features/customer/pages/infoCustomer/infoCustomer';
import OrderCustomer from './features/order/pages/orderCustomer/orderCustomer';
import CheckoutOrder from './features/order/pages/checkoutOrder/checkoutOrder';
import OrderConfirm from './features/order/pages/orderConfirm/orderConfirm';
import DetailOrderCustomer from './features/order/pages/detailOrderCustomer/detailOrderCustomer';
import Footer from './components/footer/footer';


function App() {
  return (
    <Router>
      <div>
        <Header/>
        
        {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
        <Switch>
          {/* <Redirect exact from="/" to="/home" component={Home}/> */}
          <Route exact path="/home" component={Home}/>
          <Route path="/product/detail/:id" component={DetailProduct}/>
          <Route path="/cart" component={ListCart}/>
          <Route path="/login" component={Login}/>
          <Route path="/register" component={Register}/>
          <Route path="/customer" component={InfoCustomer}/>
          <Route path="/ordercustomer" component={OrderCustomer}/>
          <Route path="/checkoutorder" component={CheckoutOrder}/>
          <Route path="/orderconfirm/:id" component={OrderConfirm}/>
          <Route path="/detailorder/:id" component={DetailOrderCustomer}/>
        </Switch>
        <Footer/>
      </div>
    </Router>
  );
}

export default App;
