import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";

import Banner from "../../../../components/banner/banner";

import "./style.scss";
import "../../../../App.scss";

function DetailProduct(props) {
  let { id } = useParams();
  const [product, setProduct] = useState([]);
  const [urlImage, setUrlImage] = useState("");
  const [quantity,setQuantity] = useState(1);
  useEffect(() => {
    axios
      .get("http://localhost:9080/sanpham/detail/" + id)
      .then((res) => {
        setProduct(res.data[0]);
        setUrlImage(require("../../../../assets/product/" + res.data[0].image));
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <div>
      <Banner title="Detail product Page" shoeImg={true} />
      <div className="container mt-4">
          <h3>Detail product</h3>
        <div className="row mt-4">
          <div className="col-md-6">
            <div className="image-product">
              <img src={urlImage} alt="img-product-detail" />
            </div>
          </div>
          <div className="col-md-6">
            <div className="content-detail p-4">
              <p className="name-product">{product.name}</p>
              <p className="price-product">{product.price}</p>
              <p className="category">Category:<span className="pl-4">{product.category}</span></p>
              <hr />
              <p>{product.description}</p>
              <form>
                  <label htmlFor="quantity">Quantity:</label>
                  <input type="number" className="quantity ml-2" value={quantity} onChange={(e)=> setQuantity(e.target.value)}/>
              </form>
              <button className="btn-addcart mt-4">add to cart</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default DetailProduct;
