import React, { useState } from "react";
import axios from "axios";
import { useDispatch } from "react-redux";
import { Redirect } from "react-router-dom";

import "./style.scss";
import { login } from "../../../../actions/userAction";
import ButtonEffect from "../../../../components/button/buttonEffect/buttonEffect";

function FormLogin(props) {
  const [username, setUsername] = useState("huuhuu1999");
  const [password, setPassword] = useState("123456");
  const [flagLogin, setFlagLogin] = useState("");
  const dispatch = useDispatch();

  const handleLogin = (username, password) => {
    axios
      .post("http://localhost:9080/user/login", {
        username: username,
        password: password,
      })
      .then((res) => {
        if (res.data.login === "success") {
          const user = {
            username: username,
            password: password,
          };
          const action = login(user);
          dispatch(action);
          setFlagLogin(<Redirect to="/cart" />);
        } else {
          setFlagLogin(
            <div className="alert alert-danger" role="alert">
              Wrong username or password!
            </div>
          );
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <div className=" form-login">
      <div className="content-login">
        <h3>Login</h3>
        <input
          type="text"
          name="username"
          placeholder="Username"
          className="input-login mb-3"
          autoComplete="off"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
        />
        <input
          type="password"
          name="password"
          placeholder="Password"
          className="input-login mb-3"
          autoComplete="off"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        {flagLogin}
        <ButtonEffect
          title="log in"
          onClick={() => handleLogin(username, password)}
        />
        {/* {flagLogin === true ? <Redirect to="/cart" /> : ""} */}
        <p className="mt-4">Forgot password?</p>
      </div>
    </div>
  );
}

export default FormLogin;
