import React from "react";

import "./style.scss";

function InputFormCheckout(props) {
  return (
    <div>
      <input
        className="form-code w-100"
        type={props.type}
        value={props.value}
        placeholder={props.placeholder}
        onChange={props.onChange}
        name={props.name}
      />
    </div>
  );
}

export default InputFormCheckout;
