import React from "react";
import moment from "moment";
import { Link } from "react-router-dom";

import "./style.scss";

function ListOrder(props) {
  const listOrder = props.list;
  let x = "";
  if (listOrder !== null) {
    x = listOrder.map((item, index) => {
      let url = "/detailorder/" + item._id;
      return (
        <tr key={index}>
          <td>{moment(item.orderDate).format("DD-MM-YYYY")}</td>
          <td>{item.product.length}</td>
          <td>{item.sumPrice - item.moneyCoupon}</td>
          <td>{item.status}</td>
          <td>
            <Link to={url} className="detail">
              Detail
            </Link>
          </td>
        </tr>
      );
    });
  } else {
  }
  return (
    <div>
      {listOrder !== null ? (
        <table className="w-100 list-order">
          <thead>
            <tr>
              <th>Order date</th>
              <th>Quantity product</th>
              <th>Total</th>
              <th>Status order</th>
              <th></th>
            </tr>
          </thead>
          <tbody>{x}</tbody>
        </table>
      ) : (
        <div className="alert alert-danger" role="alert">
          Your order empty!
        </div>
      )}
    </div>
  );
}

export default ListOrder;
