//import library
import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCartPlus } from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

//import image
import logo from "../../assets/logo.png";

//import static file
import "./style.scss";
import { logout } from "../../actions/userAction";

function Header(props) {
  const cartList = useSelector((state) => state.cart.listProduct);
  const username = useSelector((state) => state.user.user);
  const dispatch = useDispatch();
  let shortUsername = "";
  //xử lý hiển thị username
  if (username !== "") {
    shortUsername = username.substring(0, 1);
  }
  // xu ly logout
  const handleLogout = () => {
    const action = logout();
    dispatch(action);
  };
  return (
    <div className="abc mt-4">
      <div className="container header">
        <div className="logo">
          <img className="logo-img" src={logo} alt="logo" />
        </div>
        <div className="menu">
          <ul className="menu-parent">
            <Link to="/home" className="link">
              <li>Home</li>
            </Link>
            {/* <Link className="link shop-menu"> */}
            <li className="link shop-menu">
              Shop
              <ul className="sub-menu">
                <li className="link">Category</li>
                <Link to="/cart" style={{ textDecoration: "none" }}>
                  <li className="link">Cart</li>
                </Link>
              </ul>
            </li>
            {/* </Link> */}
            <Link className="link" to="">
              <li>Blog</li>
            </Link>
            <Link className="link" to="">
              <li>Contact</li>
            </Link>
            {username === "" ? (
              <Link className="link" to="/login">
                <li>Login</li>
              </Link>
            ) : (
              // <Link className="link shop-menu">
              <li className="link shop-menu">
                <span>{shortUsername}</span>
                <ul className="sub-menu">
                  <Link to="/customer" style={{ textDecoration: "none" }}>
                    <li className="link">{username}</li>
                  </Link>

                  <Link to="/ordercustomer" style={{ textDecoration: "none" }}>
                    <li className="link">View order</li>
                  </Link>
                  <Link
                    to="/home"
                    style={{ textDecoration: "none" }}
                    onClick={() => handleLogout()}
                  >
                    <li className="link">Logout</li>
                  </Link>
                </ul>
              </li>
              // </Link>
            )}

            <Link className="link" to="/cart">
              <li>
                <FontAwesomeIcon icon={faCartPlus} />: ({cartList.length})
              </li>
            </Link>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Header;
