import React from "react";
import { Link } from "react-router-dom";
import { useDispatch} from 'react-redux';

import "./style.scss";
import { addToCart } from "../../../../actions/cartAction";

function ItemProduct(props) {
  let urlImage = require("../../../../assets/product/" + props.image);
  let urlDetail = "/product/detail/"+props.id;
  
  const dispatch = useDispatch();
  const handleAddToCart = ()=>{
    const product = {
      id:props.id,
      name:props.name,
      price:props.price,
      image: props.image,
      quantity: 1,
      total:props.price
    }
    const action = addToCart(product);
    dispatch(action);
  }
  return (
    <div className="item-product mt-3">
      <Link to={urlDetail}>
        <img src={urlImage} alt="img-shoe" className="img-item-product" />
      </Link>
      <br />
      <div className="content-product pl-2">
        <span className="product-name">{props.name}</span>
        <br />
        <span className="product-price">{props.price}</span>
        <br />
        <button className="btn-addcart mb-2" onClick={()=>handleAddToCart()}>Add to cart</button>
        <br />
      </div>
    </div>
  );
}

export default ItemProduct;
