const initState = {
    user: "",
    password:""
}

const userReducer = (state=initState,action)=>{
    switch(action.type){
        case "LOGIN":
            return {
                ...state,
                user:action.payload.username,
                password: action.payload.password
            };
        case "LOGOUT":
            return{
                ...state,
                user:"",
                password:""
            }
        default:
            return state;
    }
}

export default userReducer;