import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Table } from "reactstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimesCircle } from '@fortawesome/free-solid-svg-icons'

import { deleteCart } from "../../../../actions/cartAction";

import "./style.scss";

function TableProductCart(props) {
  const cartList = useSelector((state) => state.cart.listProduct);
  const dispatch = useDispatch();


  const handleChangeQuantity = (e,id)=>{
    // const action = changeQuantity(e.target.value);
    // dispatch(action);
    alert(e.target.value);
  }
  const handleDeleteCart = (id)=>{
    const action = deleteCart(id);
    dispatch(action);
  }
  return (
      <Table>
        <thead>
          <tr className="text-center">
            <th>#</th>
            <th>Product</th>
            <th>Quantity</th>
            <th>Price</th>
            <th>Total</th>
            <th>Delete</th>
          </tr>
        </thead>
        <tbody>
          {cartList.map((item, index) => {
            return (
              <tr key={index} className="text-center">
                <th scope="row">{index}</th>
                <td>
                  <div className="name-img">
                    <img className="image-item-cart"
                      src={require("../../../../assets/product/" + item.image)}
                      alt=""
                      width={150}
                    />
                    <p className="ml-4">{item.name}</p>
                  </div>
                </td>
                <td>
                  <input name="quantity" className="quantity-product" type="number" value={item.quantity} onChange={(e)=>handleChangeQuantity(e,item.id)}/>
                </td>
                <td>{item.price}</td>
                <td>{item.total}</td>
                <td><FontAwesomeIcon icon={faTimesCircle} className="icon" onClick={()=>handleDeleteCart(item.id)}/></td>
              </tr>
            );
          })}
        </tbody>
      </Table>
  );
}

export default TableProductCart;
