import React, { useEffect, useState } from "react";
import axios from "axios";
import { useSelector } from "react-redux";
import moment from "moment";

import Banner from "../../../../components/banner/banner";
import InputForm from "../../../../components/form/input/inputForm";
import ButtonEffect from "../../../../components/button/buttonEffect/buttonEffect";

import "./style.scss";

function InfoCustomer(props) {
  const [name, setName] = useState("");
  const [birthday, setBirthday] = useState(Date.now());
  const [phone, setPhone] = useState(0);
  const [address, setAddress] = useState("");
  const [oldPass, setOldPass] = useState("");
  const [newPass, setNewPass] = useState("");
  const [reNewPass, setReNewPass] = useState("");
  const [checkbox, setCheckbox] = useState(false);
  const [idCustomer, setIdCustomer] = useState("");
  const [flagUpdate, setFlagUpdate] = useState("");
  const user = useSelector((state) => state.user.user);
  const pass = useSelector(state=>state.user.password);
  const handleUpdateCustomer = () => {
    if(oldPass !==pass){
      setFlagUpdate(
        <div className="alert alert-danger" role="alert">
          Current pass wrong!
        </div>
      );
    }
    else if (newPass !== reNewPass) {
      setFlagUpdate(
        <div className="alert alert-danger" role="alert">
          Repassword fail!
        </div>
      );
    }
    else {
      let pass = undefined;
      if (checkbox === false || newPass === "") {
        pass = undefined;
      } else {
        pass = newPass;
      }
      axios
        .post("http://localhost:9080/khachhang/updateCustomer", {
          password: pass,
          name: name,
          birthday: birthday,
          phone: phone,
          address: address,
          user: user,
          id: idCustomer,
        })
        .then((res) => {
          if (res.data.update === "success") {
            setFlagUpdate(
              <div className="alert alert-success" role="alert">
                Update success!
              </div>
            );
          } else {
            setFlagUpdate(
              <div className="alert alert-danger" role="alert">
                Update fail!
              </div>
            );
          }
        })
        .catch((err) => {
          console.log("loi update customer:", err);
        });
    }
  };

  useEffect(() => {
    if (user !== "") {
      axios
        .get("http://localhost:9080/khachhang/getIdCustomer/" + user)
        .then((res) => {
          setIdCustomer(res.data.id);
          axios
            .get("http://localhost:9080/khachhang/getkhachhang/" + res.data.id)
            .then((res) => {
              setName(res.data.khachhang[0].name);
              console.log(res.data.khachhang[0].birthday);
              setBirthday(
                moment(res.data.khachhang[0].birthday).format("YYYY-MM-DD")
              );
              setPhone(res.data.khachhang[0].phone);
              setAddress(res.data.khachhang[0].address);
            })
            .catch((err) => {
              console.log("loi getkhachhang:", err);
            });
        })
        .catch((err) => {
          console.log("loi getIdCustomer:", err);
        });
    }
  }, []);
  return (
    <div>
      <Banner title="Profile page" shoeImg={true} />
      <div className="container mt-4">
        <div className="row p-4 border">
          <div className="col-md-6 pl-4">
            <div className="mb-4">
              <InputForm
                type="text"
                placeholder="Enter your name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                name="name"
              />
            </div>
            <div className="mb-4">
              <InputForm
                type="date"
                value={birthday}
                onChange={(e) => setBirthday(e.target.value)}
                name="birthday"
              />
            </div>
            <div className="mb-4">
              <InputForm
                type="number"
                placeholder="Enter your phone"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                name="phone"
              />
            </div>
            <div className="mb-4">
              <InputForm
                type="text"
                placeholder="Enter your address"
                value={address}
                onChange={(e) => setAddress(e.target.value)}
                name="address"
              />
            </div>
          </div>
          <div className="col-md-6 pl-4">
            <div className="d-flex mb-4">
              <div className="div-checkbox">
                <InputForm
                  type="checkbox"
                  value={checkbox}
                  checked={checkbox}
                  onChange={(e) => setCheckbox(!checkbox)}
                  name="checkbox"
                />
              </div>
              <span className="font-weight-bold ml-3">Change passord</span>
            </div>
            {checkbox === true ? (
              <div>
                <div className="mb-4">
                  <InputForm
                    type="password"
                    placeholder="Enter current password"
                    value={oldPass}
                    onChange={(e) => setOldPass(e.target.value)}
                    name="oldpass"
                  />
                </div>
                <div className="mb-4">
                  <InputForm
                    type="password"
                    placeholder="Enter new password"
                    value={newPass}
                    onChange={(e) => setNewPass(e.target.value)}
                    name="newpass"
                  />
                </div>
                <div className="mb-4">
                  <InputForm
                    type="password"
                    placeholder="Re-type new password"
                    value={reNewPass}
                    onChange={(e) => setReNewPass(e.target.value)}
                    name="reNewPass"
                  />
                </div>
              </div>
            ) : (
              <div>
                <div className="mb-4">
                  <InputForm
                    type="password"
                    placeholder="Enter current password"
                    value={oldPass}
                    onChange={(e) => setOldPass(e.target.value)}
                    name="oldpass"
                    disabled
                  />
                </div>
                <div className="mb-4">
                  <InputForm
                    type="password"
                    placeholder="Enter new password"
                    value={newPass}
                    onChange={(e) => setNewPass(e.target.value)}
                    name="newpass"
                    disabled
                  />
                </div>
                <div className="mb-4">
                  <InputForm
                    type="password"
                    placeholder="Re-type new password"
                    value={reNewPass}
                    onChange={(e) => setReNewPass(e.target.value)}
                    name="reNewPass"
                    disabled
                  />
                </div>
              </div>
            )}
          </div>
          <div className="w-100">{flagUpdate}</div>

          <div className="ml-4">
            <ButtonEffect title="save" onClick={() => handleUpdateCustomer()} />
          </div>
        </div>
      </div>
    </div>
  );
}

export default InfoCustomer;
