import React, { useState } from "react";
import axios from "axios";
import { useDispatch, useSelector } from "react-redux";

import ButtonEffect from "../../../../components/button/buttonEffect/buttonEffect";
import InputFormCheckout from "../inputFormCheckout/inputFormCheckout";
import { addCoupon } from "../../../../actions/cartAction";

function FormCoupon(props) {
  const [code, setCode] = useState("");
  const sumPrice = useSelector((state) => state.cart.sumPrice);
  const dispatch = useDispatch();
  const [alert,setAlert]= useState("");

  const handleAddCoupon = () => {
    axios
      .post("http://localhost:9080/magiamgia/addcoupon", {
        code: code,
      })
      .then((res) => {
        if (res.data.check === "success") {
          let x = sumPrice * 0.1;
          const action = addCoupon(x);
          dispatch(action);
        } else {
          setAlert(
            <div class="alert alert-danger mt-2" role="alert">
              Coupon is wrong!
            </div>
          );
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <div>
      <InputFormCheckout
        value={code}
        placeholder="Enter your coupon"
        onChange={(e) => setCode(e.target.value)}
        name="code"
        type="text"
      />
      {alert}
      <div className="w-25 mt-3">
        <ButtonEffect
          title="apply coupon"
          onClick={() => {
            handleAddCoupon();
          }}
        />
      </div>
    </div>
  );
}

export default FormCoupon;
